import React, {Component} from 'react';
import {Card} from "react-bootstrap";
import {Virtuoso} from 'react-virtuoso';
import Item from "./AllCourseList/Item";

class CoursesList extends Component {
    /**
     * 
     * @param {number} index
     * @returns {JSX.Element}
     */
    renderItem = (index) => {
        const {
            courses,
            displaySelectedOnly,
            selectedCourses,
            displayConflictCourses,
            detectTimeConflict,
            onCourseSelect,
            onCourseHover,
            hoveredCourseId,
            isCollapsed
        } = this.props;

        const course = courses[index];

        // 
        if (!course) return null;

        const isSelected = selectedCourses.has(course);
        const isHovered = hoveredCourseId === course['Number'];
        let isConflict = false;

        // 
        if (displaySelectedOnly && !isSelected) {
            return null;
        }

        // 
        if (displayConflictCourses) {
            isConflict = detectTimeConflict(course, selectedCourses);
        }

        // 
        return (
            <Item
                isCollapsed={isCollapsed}
                course={course}
                isSelected={isSelected}
                isConflict={isConflict}
                isHovered={isHovered}
                onCourseSelect={onCourseSelect}
                onCourseHover={onCourseHover}
            />
        );
    }

    render() {
        const {courses} = this.props;

        if (courses.length === 0) {
            return <Card.Text className="text-center p-5"></Card.Text>;
        }

        return (
            <Virtuoso
                data={courses}
                itemContent={this.renderItem}
            >
            </Virtuoso>
        );
    }
}

export default CoursesList;
